import React from "react";
import "./CurrencyIcon.css";

const CurrencyIcon = ({ currency, size = 15 }: any) => (
  <div
    className="CurrencyIcon"
    style={{
      width: size,
      height: size,
      backgroundImage: `url(https://botanapp.s3.amazonaws.com/coinicons/${size}/${currency.toLowerCase()}.png)`,
    }}
  ></div>
);

export default CurrencyIcon;
