import React from "react";
import { Routes, Route } from "react-router-dom";
import App from "./App";
import { DCABotDetails } from "./components/DCABots/DCABotDetails/DCABotDetails";
import { DCABots } from "./components/DCABots/DCABots";
import GridBots from "./components/GridBots/GridBots";

const Router = () => {
  return (
    <Routes>
      <Route path="/" element={<App />}>
        <Route index element={<DCABots />} />
        <Route path=":botId" element={<DCABotDetails />} />
        <Route path="/gridbots" element={<GridBots />} />
      </Route>
    </Routes>
  );
};

export default Router;

